:root {
  --accent: #cc8869;
  --text: #333;
  --regular-text: 16px;
  --lineheight: 1.65;
  --userfont: roboto-st, sans-serif;
  --systemfont: -apple-system, BlinkMacSystemFont, Arial, sans-serif;
  --padding: 120px;
  --transition: cubic-bezier(.4, 0, 0, 1);
}

.container, .container-fluid, .container-lg, .container-md, .container-sm, .container-xl, .container-xxl {
  --bs-gutter-x: .625rem;
}

.row, .row > * {
  --bs-gutter-x: 1.25rem;
}

@font-face {
  src: url("roboto-regular-webfont.cc1e9f53.woff2") format("woff2");
  font-family: roboto-st;
  font-weight: 400;
  font-style: normal;
}

@font-face {
  src: url("roboto-italic-webfont.4b4a7be9.woff2") format("woff2");
  font-family: roboto-st;
  font-weight: 400;
  font-style: italic;
}

@font-face {
  src: url("roboto-bold-webfont.2456e51b.woff2") format("woff2");
  font-family: roboto-st;
  font-weight: 700;
  font-style: normal;
}

@font-face {
  src: url("roboto-bolditalic-webfont.9dbc77d3.woff2") format("woff2");
  font-family: roboto-st;
  font-weight: 700;
  font-style: italic;
}

@font-face {
  src: url("assassin.1648ee8e.woff2") format("woff2");
  font-family: assassin-st;
  font-weight: 700;
  font-style: italic;
}

::placeholder {
  color: #666;
}

::selection {
  background-color: var(--accent);
  color: #fff;
}

input, textarea {
  outline: none;
}

input:focus:required:invalid, textarea:focus:required:invalid {
  border-color: red;
}

input:required:valid, textarea:required:valid {
  border-color: green;
}

body {
  font-family: var(--userfont);
  font-size: var(--regular-text);
  line-height: var(--lineheight);
  color: var(--text);
  min-width: 320px;
  position: relative;
  overflow-x: hidden;
}

